import { createRouter, createWebHashHistory } from 'vue-router'
// import HomeView from '../views/HomeView.vue'
import Template01 from '@/views/Template01.vue'
import TemplateOneItems from '@/components/template01/TemplateOneItems.vue'
import TemplateOneCart from '@/components/template01/TemplateOneCart.vue'
import TemplateOneMissions from '@/components/template01/TemplateOneMissions.vue'
import TemplateOneManage from '@/components/template01/TemplateOneManage.vue'
import TemplateOneBalance from '@/components/template01/TemplateOneBalance.vue'
import TemplateOneStock from '@/components/template01/TemplateOneStock.vue'
import TemplateOneName from '@/components/template01/TemplateOneName.vue'
import TemplateOneColor from '@/components/template01/TemplateOneColor.vue'
import TemplateOneSell from '@/components/template01/TemplateOneSell.vue'
import TemplateOneBuy from '@/components/template01/TemplateOneBuy.vue'
import TemplateOneWorkers from '@/components/template01/TemplateOneWorkers.vue'

import AdminMain from '@/views/AdminMain.vue'
import SeeStores from '@/views/SeeStores.vue'
import CreateStore from '@/views/CreateStore.vue'
import NewStore from '@/views/NewStore.vue'
import NewType from '@/views/NewType.vue'
import EditType from '@/views/EditType.vue'

const routes = [
    {
        path: '/',
        name: 'home',
        component: AdminMain
    },
    {
        path: '/admin',
        name: 'admin',
        component: AdminMain
    },
    {
        path: '/admin/see-stores',
        name: "seeStores",
        component: SeeStores
    },
    {
        path: '/admin/create-store',
        name: "createStore",
        component: CreateStore
    },
    {
        path: '/admin/new-store',
        name: "newStore",
        component: NewStore
    },
    {
        path: '/admin/new-type/:id',
        name: "newType",
        props: true,
        component: NewType
    },
    {
        path: '/admin/edit-type',
        name: "editType",
        component: EditType
    },
    {
        path: '/template01',
        name: 'template01',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: Template01,
        children: [
            {
                name: "itemsTemplate01",
                path: "",
                component: TemplateOneItems
            },
            {
                name: "cartTemplate01",
                path: "cart",
                component: TemplateOneCart
            },
            {
                name: "missionsTemplate01",
                path: "missions",
                component: TemplateOneMissions
            },
            {
                name: "manageTemplate01",
                path: "manage",
                component: TemplateOneManage,
            },
            {
                name: "balanceTemplate01",
                path: "manage/balance",
                component: TemplateOneBalance,
            },
            {
                name: "stockTemplate01",
                path: "manage/stock",
                component: TemplateOneStock,
            },
            {
                name: "nameTemplate01",
                path: "manage/name",
                component: TemplateOneName,
            },
            {
                name: "colorTemplate01",
                path: "manage/color",
                component: TemplateOneColor,
            },
            {
                name: "sellTemplate01",
                path: "manage/sell",
                component: TemplateOneSell,
            },
            {
                name: "buyTemplate01",
                path: "manage/buy",
                component: TemplateOneBuy,
            },
            {
                name: "workersTemplate01",
                path: "manage/workers",
                component: TemplateOneWorkers,
            }
        ]
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router
